input, textarea {
  width: 100%;
  height: 46px;
  border: 1px solid transparent;
  border-radius: 12px;
  background-color: $light-grey;
  padding: 11px;
  appearance: none;
  @include text-normal;
  color: $dark;
  transition: all $transition;
  outline: none;
  cursor: pointer;
  // &:hover {
  //   border-color: $almost-black-40;
  // }
  &:focus, &:active {
    outline: none;
    border-color: $main-blue;
  }
  // &:checked {
  //   ~ .label {
  //     &__box {
  //       border-color: $almost-black;
  //       &::after {
  //         opacity: 1;
  //       }
  //     }
  //   }
  // }
  &::placeholder {
    color: $grey;
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  &[required] ~ .label__title::after {
    opacity: 1;
  }
}

