.label {
  &_checkbox {
    // width: auto;
    flex-direction: row;
    gap: 12px;
    -webkit-tap-highlight-color: transparent;
    &:hover {
      .label {
        &__box {
          border-color: $almost-black;
        }
      }
    }
    // input {
    //   &:active {
    //     ~ .label {
    //       &__box {
    //         border-color: $active;
    //       }
    //       &__text {
    //         color: $active;
    //       }
    //     }
    //   }
    // }
  }
  &__box {
    margin-top: 4px;
    width: 16px;
    height: 16px;
    flex-shrink: 0;
    border: 1px solid $almost-black-20;
    background-color: $white;
    overflow: hidden;
    position: relative;
    transition: all $transition;
    @media (max-width: 1600px) {
      margin-top: 2px;
    }
    &::after {
      content: '';
      position: absolute;
      inset: 2px;
      background-color: $almost-black;
      opacity: 0;
      transition: all $transition;
    }
  }
  &__text {
    @include caption;
    a {
      text-decoration: underline;
    }
  }
}