@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin pposans {
  font-family: 'PP Object Sans', sans-serif;
}

@mixin section {
  padding: 24px 0;
  @media (max-width: 767px) {
    padding: 16px 0;
  }
}

@mixin gradient {
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 60%;
    background: linear-gradient(180deg, rgba(#2c2c38, 0) 0%, #2c2c38 100%);
    z-index: -1;
    pointer-events: none;
  }
  
}

@mixin h1 {
  font-size: 44px;
  @media (max-width: 1279px) {
    font-size: 36px;
  }
  @media (max-width: 767px) {
    font-size: 32px;
  }
}

@mixin h2 {
  font-size: 36px;
  @media (max-width: 1279px) {
    font-size: 30px;
  }
  @media (max-width: 767px) {
    font-size: 24px;
  }
}

@mixin h3 {
  font-size: 24px;
  @media (max-width: 1279px) {
    font-size: 20px;
  }
  @media (max-width: 767px) {
    font-size: 18px;
  }
}

@mixin h4 {
  font-size: 18px;
  @media (max-width: 767px) {
    font-size: 16px;
  }
}

@mixin text-normal {
  font-size: 16px;
}

@mixin text-small {
  font-size: 14px;
}

@mixin label {
  font-size: 12px;
}




@mixin h4-sans {
  font-size: 36px;
  font-weight: 500;
  letter-spacing: -.02em;
  @media (max-width: 1919px) {
    font-size: 32px;
  }
  @media (max-width: 1279px) {
    font-size: 26px;
  }
}

@mixin h5-sans {
  font-size: 28px;
  font-weight: 500;
  @media (max-width: 1919px) {
    font-size: 24px;
  }
  @media (max-width: 1279px) {
    font-size: 20px;
  }
}

@mixin h6-sans {
  font-size: 22px;
  font-weight: 500;
  @media (max-width: 1919px) {
    font-size: 20px;
  }
  @media (max-width: 1279px) {
    font-size: 18px;
  }
}

@mixin lead {
  font-size: 26px;
  line-height: 1.38;
  @media (max-width: 1919px) {
    font-size: 24px;
  }
  @media (max-width: 1279px) {
    font-size: 18px;
    line-height: 1.22;
  }
}

@mixin body {
  font-size: 18px;
  line-height: 1.33;
  @media (max-width: 1919px) {
    font-size: 16px;
    line-height: 1.31;
  }
  @media (max-width: 1279px) {
    font-size: 14px;
    line-height: 1.29;
  }
}

@mixin body-medium {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.28;
  @media (max-width: 1919px) {
    font-size: 16px;
    line-height: 1;
  }
  @media (max-width: 1279px) {
    font-size: 14px;
    line-height: 1.21;
  }
}

@mixin body-small-medium {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.375;
  @media (max-width: 1919px) {
    font-size: 14px;
    line-height: 1;
  }
  @media (max-width: 1279px) {
    font-size: 12px;
  }
}

@mixin caption {
  font-size: 16px;
  line-height: 1.31;
  @media (max-width: 1919px) {
    font-size: 14px;
    line-height: 1.21;
  }
}

@mixin caption-small {
  font-size: 14px;
  line-height: 1.29;
  @media (max-width: 1919px) {
    font-size: 12px;
    line-height: 1.42;
  }
}

@mixin button {
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -.02em;
  @media (max-width: 1919px) {
    font-size: 14px;
  }
}

@mixin button-small {
  font-size: 15px;
  font-weight: 500;
  line-height: 1.4;
  @media (max-width: 1919px) {
    font-size: 12px;
  }
}

@mixin number {
  font-size: 160px;
  @include ttnpro;
  line-height: 1;
  letter-spacing: -.01em;
  @media (max-width: 1919px) {
    font-size: 120px;
  }
  @media (max-width: 1279px) {
    font-size: 72px;
  }
}

@mixin h-big {
  font-size: 96px;
  @include ttnpro;
  line-height: .98;
  letter-spacing: -.02em;
  @media (max-width: 1919px) {
    font-size: 84px;
  }
  @media (max-width: 1279px) {
    font-size: 46px;
  }
}

@mixin shadow {
  box-shadow: 0 2px 20px rgba(#000, .07);
}